import JsUtil from 'tsx/common/JsUtil'

let per = {}

const ORG_TYPE_SP_G = 1;
per.ORG_TYPE_SP_G = ORG_TYPE_SP_G;

const ORG_TYPE_SP_R = 3;
per.ORG_TYPE_SP_R = ORG_TYPE_SP_R;

const ORG_TYPE_SP_B = 5;
per.ORG_TYPE_SP_B = ORG_TYPE_SP_B;

const ORG_TYPE_HQ = 7;
per.ORG_TYPE_HQ = ORG_TYPE_HQ;

const ORG_TYPE_OP = 9;
per.ORG_TYPE_OP = ORG_TYPE_OP;

const ORG_TYPE_SS = 11;
per.ORG_TYPE_SS = ORG_TYPE_SS;

const ORG_TYPE_MB = 13;
per.ORG_TYPE_MB = ORG_TYPE_MB;

const ORG_NAME_SUPPLIER = 'supplier';
per.ORG_NAME_SUPPLIER = ORG_NAME_SUPPLIER;

const ORG_NAME_OPERATOR = 'operator';
per.ORG_NAME_OPERATOR = ORG_NAME_OPERATOR;

const ORG_NAME_HEADQUARTER = 'headquarter';
per.ORG_NAME_HEADQUARTER = ORG_NAME_HEADQUARTER;

const ORG_NAME_STORE = 'store';
per.ORG_NAME_STORE = ORG_NAME_STORE;

const ORG_NAME_MEMBER = 'member';
per.ORG_NAME_MEMBER = ORG_NAME_MEMBER;

const getCurrOrgType = () => {
  let currentOrgType = JsUtil.getAppItem('currOrgType');
  return currentOrgType;
}
per.getCurrOrgType = getCurrOrgType;

const hasRole = authCode => {
  const authorities = JsUtil.getAppItem('authorities');
  if(!authorities) return false;
  
  let i = authorities.length;
  while (i--) {
    if (authorities[i] == authCode) {
      //console.log(authorities[i].authority);
      return true;
    }
  }

  return false;
}
per.hasRole = hasRole;

const getRoleByFlag = flag => {
  switch (flag) {
    case per.ORG_TYPE_SP_B:
    case per.ORG_TYPE_SP_G:
    case per.ORG_TYPE_SP_R:
      return 'supplier';
    case per.ORG_TYPE_MB:
      return 'member';
    case per.ORG_TYPE_OP:
      return 'operator';
    case per.ORG_TYPE_SS:
      return 'store';
    case per.ORG_TYPE_HQ:
      return 'headquarter';
    default:
      return 'member';
  }
}
per.getRoleByFlag = getRoleByFlag;

const judgeSellerByFlag = flag => {
  const role = getRoleByFlag(flag);
  return role == 'supplier';
}
per.judgeSellerByFlag = judgeSellerByFlag;

const judgeBuyerByFlag = flag => {
  const role = getRoleByFlag(flag);
  return role != 'supplier' && role != 'headquarter';
}
per.judgeBuyerByFlag = judgeBuyerByFlag;

