  SP_COMMODITY_B2C_R1: '213071', // B2C地方商城发布商品权限
  SP_FINANCE_WITHDRAW_G4: '121111', // 全国商城提现权限
  SP_FINANCE_WITHDRAW_R4: '221111', // 地方商城提现权限
  SP_FINANCE_REPORT_G4: '121814', // 全国商城明细财务报表权限
  SP_FINANCE_REPORT_R4: '221814', // 地方商城明细财务报表权限
  SP_ORDER_REPORT_G4: '119814', // 全国商城明细销售报表权限
  SP_ORDER_REPORT_R4: '219814', // 地方商城明细销售报表权限

