  HQ_SUPPLIER_APPROVAL: '325217', // 审批供应商加盟权限
  HQ_OPERATOR_APPROVAL: '329217', // 审批运营中心加盟权限
  HQ_STORE_APPROVAL: '331217', // 审批服务站加盟权限
  HQ_FINANCE_WITHDRAW_APPROVAL: '321217', // 审批提现
  HQ_FINANCE_REPORT: '321814', // 明细财务报表权限
  HQ_ORDER_REPORT: '319814', // 明细销售报表权限

  OP_BASIC: '500004', // 基本权限
