import React from 'react'
import { styled } from '@linaria/react'
import { CheckboxGroup, Checkbox } from '@tarojs/components'
import JsUtil from 'tsx/common/JsUtil'
import cnst from 'tsx/common/Constant'
import RowLayout from 'tsx/layout/RowLayout'
import TextLabel from 'tsx/view/TextLabel';

const CheckboxGroup0 = styled(CheckboxGroup)`
    display: flex;
    flex-direction: row;
    align-items: center;
    flex-wrap: wrap;
`
const Checkbox0 = styled(Checkbox)`
    display: flex;
    flex-direction: row;
    align-items: center;
    & > .taro-checkbox_checked {
    top: 0;
    width: 18px;
    height: 18px;
  }
`

class CheckBoxes extends React.Component {
    onGroupChanged = (event) => {
       console.log('CheckBoxes onGroupChanged')
       const selectedValues = event.detail.value;
       this.props.onChange(this.props.name, selectedValues)
    }
    
    onOptionChanged = (event) => {
        console.log('CheckBoxes onOptionChanged')
        const name = this.props.name;
        let selectedValues = this.props.value;
        const changedValue = event.value;
        const isChecked = event.checked;
        
        if(isChecked){
            selectedValues.push(changedValue);
        }else{
            selectedValues=JsUtil.delEleByValue(selectedValues, changedValue);
        }
        
        this.props.onChange(name, selectedValues)
     }
    
  render() {
    const width = JsUtil.getCssWidthValue(this.props.width, 'auto');
    const height = JsUtil.getCssWidthValue(this.props.height, 'auto');
	const options = this.props.options;
    const value = this.props.value;

    return (
    <CheckboxGroup0 onChange={this.onGroupChanged}>
    {options.map((option, idx) => {
     const isChecked = value? (value.includes(option.value)||value.includes(option.value+''))? true: false: false;
     
     return (
      <Checkbox0 key={idx} value={option.value} checked={isChecked} onChange={this.onOptionChanged}
       style={{width: width, height: height, fontSize: 14}}
      >
      {option.label}&nbsp;&nbsp;
      </Checkbox0>
     )})}
    </CheckboxGroup0>
    )
  }
}

export default CheckBoxes
