import React from 'react'
import CategoryLayout from 'tsx/layout/CategoryLayout'
import Level2View from 'tsx/view/Level2View'

class FloorLayout extends React.Component {

  render() {
    return (
      <CategoryLayout messages={this.props.messages} >
          {this.props.children}
      </CategoryLayout>
    )
  }
}

export default FloorLayout
