import React from 'react'
import cnst from 'tsx/common/Constant'
import BaseView from 'tsx/view/BaseView'
import ColumnLayout from 'tsx/layout/ColumnLayout'
import RowLayout from 'tsx/layout/RowLayout'
import TextLabel from 'tsx/view/TextLabel'

class TabPane extends React.Component {
  onClick = (idx) => (event) =>{
      
      this.props.onClick(idx);
  }
  
  render() {
    const tabOptions = this.props.tabOptions;  
    const currTabIdx = this.props.currTabIdx;
    return (
      <BaseView width={cnst.NUM_WIDTH_MAIN} height={'auto'} >
            <RowLayout justifyContent='space-between'>
            {tabOptions.map((item, idx)=>{
                return (<TextLabel key={idx} onClick={this.onClick(idx)} color={currTabIdx==idx? 'red': 'black'}>{item.title}&nbsp;&nbsp;</TextLabel>)
            })
            }
            </RowLayout>
            {this.props.children}
      </BaseView>
    )
  }
}

export default TabPane
