const getDeliveryName = (deliveryType) =>{
    let deliveryTypeStr = '';
    
     switch (deliveryType) {
      case cnst.DELIVERY_TYPE_EXPRESS:
        deliveryTypeStr = '快递';
        break;
      case cnst.DELIVERY_TYPE_EXPRESS_CHANGE:
        deliveryTypeStr = '转运';
        break;
      case cnst.DELIVERY_TYPE_IMMEDIATELY:
        deliveryTypeStr = '即时送';
        break;
      case cnst.DELIVERY_TYPE_SELF_PICKUP:
        deliveryTypeStr = '自提';
        break;
      case cnst.DELIVERY_TYPE_ONLINE:
        deliveryTypeStr = '在线运输';
        break;
	  default:
		deliveryTypeStr = ''
		break
	}
	
	return deliveryTypeStr;
}
cnst.getDeliveryName = getDeliveryName;

export default cnst;
