import React from 'react'
import HeadLayout from 'tsx/layout/HeadLayout'
import Level2View from 'tsx/view/Level2View'

class CharacteristicLayout extends React.Component {

  render() {
    return (
      <HeadLayout messages={this.props.messages}>
          {this.props.children}
      </HeadLayout>
    )
  }
}

export default CharacteristicLayout
