import React from 'react'
import JsUtil from 'tsx/common/JsUtil'
import cnst from 'tsx/common/Constant'
import DivView from 'tsx/view/DivView'
import RowLayout from 'tsx/layout/RowLayout'
import ColumnLayout from 'tsx/layout/ColumnLayout'
import TextLabel from 'tsx/view/TextLabel'
import LegenImage from 'tsx/view/LegenImage'

class CommodityLayout extends React.Component {
   onClick = (item) => (event) =>{
       this.props.onClick(item, 0)
   }
   
  render() {
    const shownObj = this.props.shownObj
    if(!shownObj) return null;
    const imgPrefix = JsUtil.IMAGE_PREFIX;
    const commodityId = shownObj.commodityId
    const width = cnst.NUM_WIDTH_MAIN/2;
    const height = cnst.NUM_HEIGHT_MAIN/2;

    return (
      <ColumnLayout width={width}>
            <LegenImage src={imgPrefix + shownObj.commodityPicUri} onClick={this.onClick(shownObj)} width={width} height={height} />
            <DivView width={width+2}><TextLabel width={width+2} flexWrap='wrap'>{shownObj.commodityName}</TextLabel></DivView>
            <RowLayout width={cnst.NUM_WIDTH_MAIN/2}>
            <TextLabel color='red'>{JsUtil.currFormat(shownObj.price)}&nbsp;</TextLabel>
            <TextLabel>{shownObj.awardRate*100}&nbsp;</TextLabel>
            <TextLabel color='red'>{shownObj.presentRate*100}&nbsp;</TextLabel>
            <TextLabel>{shownObj.voucherRate*100}</TextLabel>
            </RowLayout>
      </ColumnLayout>
    )
  }
}

export default CommodityLayout
