import React from 'react'
import Modal from 'tsx/component/CustomModal';
import JsUtil from 'tsx/common/JsUtil'
import cnst from 'tsx/common/Constant'
import BaseView from 'tsx/view/BaseView'
import ScrollableView from 'tsx/view/ScrollableView'
import RowLayout from 'tsx/layout/RowLayout'
import ColumnLayout from 'tsx/layout/ColumnLayout'
import CheckBoxes from 'tsx/form/CheckBoxes';
import GeneralButton from 'tsx/form/GeneralButton'
import TextLabel from 'tsx/view/TextLabel'

class TaroModal extends React.Component {
    state = {
        checkedList: []
    }

    onCheckBoxesChanged = (name, values) => {
        this.setState({ [name]: values });
        this.props.onCheckLevelAll();
    }

    showMessages = (contents) => {
      return contents.map((item, idx) =>{
                            return (<TextLabel key={idx}>{item}</TextLabel>)
                        })
    }

    close = (event) => {
	  console.log('TaroModal clos() event: ', event);
	  if(this.props.that){
	    const messages = this.props.that.state? this.props.that.state.messages:{};
        this.props.that.setState({messages: {...messages, isOpened:false}});
      }
    }
    
    render() {
        const isRN = process.env.TARO_ENV === 'rn';
        let isOpened = this.props.isOpened? true: false;
        let display = this.props.isOpened? "flex" :  "none"
        const zIndex = JsUtil.getCssNumValue( this.props.zIndex, 6666);
              
        const justifyContent = JsUtil.getCssStrValue( this.props.justifyContent, 'center');

        let alignItems = this.props.alignItems
        alignItems ? alignItems = alignItems : alignItems = 'center'
              
        const backgroundColor = JsUtil.getColorValue(this.props.backgroundColor, cnst.COLOR_BACKGROUND_TRANS_WHITE);
          
        const cancelText = this.props.cancelText;
        const confirmText = this.props.confirmText;
        const checkText = this.props.checkText;
        let onConfirm = this.props.onConfirm;
        const options = [];
        options.push({ value: 1, label: checkText });
        
        const messages = this.props.that? this.props.that.state.messages:{};
        let contents = [];
        let shownContent = this.props.children;
        let height = JsUtil.getCssNumValue(this.props.height, cnst.NUM_HEIGHT_MAIN)
        if(messages && messages.content){
            contents = messages.content.split('\n');
            shownContent=this.showMessages(contents);
            onConfirm = messages.onConfirm||this.close;
            display = 'flex';
            isOpened = messages.isOpened;
            height = cnst.NUM_HEIGHT_MAIN/2
        }
        
        const menuBar = [<RowLayout justifyContent={'space-between'} width={cnst.NUM_WIDTH_MAIN} height={-5} backgroundColor={[0,255,255,1]}>
                        {this.props.onCheckLevelAll && <CheckBoxes name='checkedList' options={options} value={this.state.checkedList} onChange={this.onCheckBoxesChanged}  width={68} />}
                        {this.props.onClose && <GeneralButton type='primary' size='mini' width={26} height={20} onClick={this.props.onClose}>关闭</GeneralButton>}
                        {this.props.onCancel && <GeneralButton type='primary' size='mini' width={26} height={20} onClick={this.props.onCancel}>{cancelText ? cancelText : '取消'}</GeneralButton>}
                        {onConfirm && <GeneralButton type='primary' size='mini' width={26} height={20} onClick={onConfirm}>{confirmText ? confirmText : '确定'}</GeneralButton>}
                         </RowLayout>];

        if(isRN==false){
		if(!isOpened) return '';
        return  (
                <BaseView  display={display} position='fixed' zIndex={zIndex} width={cnst.NUM_WIDTH_MAIN}>
                <ColumnLayout width={cnst.NUM_WIDTH_MAIN} backgroundColor={[173,255,47,1]}>
                <ScrollableView justifyContent={justifyContent} alignItems={alignItems} height={height+20}>
                    {shownContent}
                </ScrollableView>
                {menuBar}
                </ColumnLayout>
                </BaseView>
           )}else{
           return (
                <Modal isVisible={isOpened} 
          animationIn={'fadeIn'}
          animationOut={'fadeOut'}
          coverScreen={true}
          backdropColor={'white'}
          backdropOpacity={0}> 
          <ColumnLayout  justifyContent={'center'} alignItems='center' width={cnst.NUM_WIDTH_MAIN}  height={height} backgroundColor={[255,0,255,1]}>
                <ScrollableView justifyContent={this.props.justifyContent} height={height+15}>
                    {shownContent}
                </ScrollableView>
                {menuBar}
         </ColumnLayout>
         </Modal>)
    }
    }
}

export default TaroModal
