import React from 'react'
import JsUtil from 'tsx/common/JsUtil'
import cnst from 'tsx/common/Constant'
import BrView from 'tsx/view/BrView'
import ColumnLayout from 'tsx/layout/ColumnLayout'
import RowLayout from 'tsx/layout/RowLayout'
import TextLabel from 'tsx/view/TextLabel'
import LinkTag from 'tsx/view/LinkTag'

class ListItem extends React.Component {
  render() {
      const justifyContent =  JsUtil.getCssStrValue(this.props.justifyContent, 'flex-start')
      const alignItems =  JsUtil.getCssStrValue(this.props.alignItems, 'stretch')
      const alignSelf =  JsUtil.getCssStrValue(this.props.alignSelf, 'auto')
  
    return (<ColumnLayout>
      <RowLayout  
        arrow={this.props.arrow} 
        hasBorder={this.props.hasBorder}
        disabled={false}
              justifyContent={justifyContent}
              alignItems={alignItems}
              alignSelf={alignSelf}
        {...this.props}
       >
            <LinkTag  onClick={this.props.onClick}>{this.props.title} </LinkTag>
            <TextLabel>{this.props.extraText} </TextLabel>
      </RowLayout>
      <BrView height={cnst.NUM_HEIGHT_LINE} backgroundColor={cnst.COLOR_BACKGROUND_TRANS_WHITE} />
      </ColumnLayout>
    )
  }
}

export default ListItem
