import React from 'react'
import JsUtil from 'tsx/common/JsUtil'
import ColumnLayout from 'tsx/layout/ColumnLayout'

class Level2View extends React.Component {
  render() {
      const justifyContent =  JsUtil.getCssStrValue(this.props.justifyContent, 'flex-start')
      const alignItems =  JsUtil.getCssStrValue(this.props.alignItems, 'stretch')
      const alignSelf =  JsUtil.getCssStrValue(this.props.alignSelf, 'auto')
      
      return (
              <ColumnLayout 
              justifyContent={justifyContent}
              alignItems={alignItems}
              alignSelf={alignSelf}
              {...this.props}
              >
              {this.props.children}
              </ColumnLayout >
          )
  }
}

export default Level2View
