import React from 'react'
import { styled } from '@linaria/react'
import { CheckboxGroup, Checkbox, View } from '@tarojs/components'
import JsUtil from 'tsx/common/JsUtil'
import cnst from 'tsx/common/Constant'
import BrView from 'tsx/view/BrView'
import BaseView from 'tsx/view/BaseView'
import RowLayout from 'tsx/layout/RowLayout'
import ColumnLayout from 'tsx/layout/ColumnLayout'
import TextLabel from 'tsx/view/TextLabel'

const CheckboxGroup0 = styled(CheckboxGroup)`
    display: flex;
    flex-direction: row;
    align-items: center;
    flex-wrap: wrap;
`
const Checkbox0 = styled(Checkbox)`
    display: flex;
    flex-direction: row;
    align-items: center;
    & > .taro-checkbox_checked {
    top: 0;
    width: 18px;
    height: 18px;
  }
`

class CascadeBox extends React.Component {
    constructor(props) {
      super(props);
      this.state = {
        checkedList: [],
      }
    }
    
    onGroupChanged = (event) => {
       console.log('CascadeBox onGroupChanged')
       const id = event.currentTarget.id;
       const name = this.props.name;
       const selectedValues = event.detail.value;
       let checkedList = this.props.value || [];
       const ids = id.split('_');
       const idx = parseInt(ids[0]);
       const idxp = parseInt(ids[1]);
       const changedValue = ids[2];
       
       if(selectedValues.length>0){
           checkedList.push(changedValue);
       }else{
           checkedList=JsUtil.delEleByValue(checkedList, changedValue);
       }
       
       //this.setState({checkedList: checkedList});
       this.props.onChange(name, checkedList, changedValue, idx, idxp);
    }
    
    onOptionChanged = (event) => {
        console.log('CascadeBox onOptionChanged')
        const name = this.props.name;
        let checkedList = this.props.value;
        const selectedValues = event.value;
        const isChecked = event.checked;
        const ids = selectedValues.split('_');
        const idx = parseInt(ids[0]);
        const idxp = parseInt(ids[1]);
        const changedValue = ids[2];
        
        if(isChecked){
            checkedList.push(changedValue);
        }else{
            checkedList=JsUtil.delEleByValue(checkedList, changedValue);
        }
        
        //this.setState({checkedList: checkedList});
        this.props.onChange(name, checkedList, changedValue, idx, idxp);
     }
    
  render() {
    const justifyContent = this.props.justifyContent? this.props.justifyContent: 'flex-start';
    const marginLeft = this.props.marginLeft? this.props.marginLeft: 3;
    const width = JsUtil.getCssWidthValue(this.props.width, cnst.NUM_WIDTH_MAIN);
    const height = JsUtil.getCssWidthValue(this.props.height, 'auto');
    const name = this.props.name;
    const value = this.props.value;
    const idxp = this.props.idxp? this.props.idxp: 0 ;
    const options = this.props.options;
      
    return (<BaseView flexDirection={this.props.flexDirection} marginLeft={marginLeft} flexWrap='wrap' width={width} height={height} backgroundColor={cnst.COLOR_BACKGROUND_TRANS_WHITE}>
     {this.props.label &&
       <TextLabel backgroundColor={cnst.COLOR_BACKGROUND_TRANS_WHITE}>{this.props.label}</TextLabel>
     }

     {options.map((option, idx) => {
     const isChecked = value? value.includes(option.value)? true: false: false;
     
     if(option.desc){
     return (
          <ColumnLayout key={idx}>
          <CheckboxGroup0 id={idx+'_'+idxp+'_'+option.value}  onChange={this.onGroupChanged} disabled={this.props.disabled}>
          <RowLayout alignItems='center' height={23}>
          <Checkbox0 value={idx+'_'+idxp+'_'+option.value} checked={isChecked}  onChange={this.onOptionChanged}
           style={{fontSize: 14}} disabled={this.props.disabled}
          />
          {option.label}
          </RowLayout>
          </CheckboxGroup0>
          <BrView height={cnst.NUM_HEIGHT_LINE} backgroundColor={cnst.COLOR_BACKGROUND_TRANS_WHITE} />
          <BaseView justifyContent='space-around'>{option.desc}</BaseView>
          {!option.desc.props.options &&
          <BrView height={1} backgroundColor={cnst.COLOR_BACKGROUND_DEEP_BLUE} />
          }
          <BrView height={cnst.NUM_HEIGHT_LINE} backgroundColor={cnst.COLOR_BACKGROUND_TRANS_WHITE} />
          </ColumnLayout>
     )}else{
     return (
          <CheckboxGroup0 id={idx+'_'+idxp+'_'+option.value}  onChange={this.onGroupChanged} disabled={this.props.disabled}>
          <Checkbox0 value={idx+'_'+idxp+'_'+option.value} checked={isChecked}  onChange={this.onOptionChanged}
           style={{fontSize: 14}} disabled={this.props.disabled}
      >
      {option.label}
      </Checkbox0>
          </CheckboxGroup0>
     )}
     
     })}

    </BaseView>
    )
  }
}

export default CascadeBox
