import React from 'react'
import BaseView from 'tsx/view/BaseView'
import TextLabel from 'tsx/view/TextLabel'

class LinkTag extends React.Component {
  render() {
    return (
      <BaseView
            flexDirection={'row'}
            {...this.props}
      >
        <TextLabel 
            color={this.props.color} 
            onClick={this.props.onClick}>{this.props.children}
        </TextLabel>
      </BaseView>
    )
  }
}

export default LinkTag
