const asyncHttpPost = (that, uri, param, succ, err, contentType = 'application/json') => {
	if (!JsUtil.checkLogin(that, uri)) return;

	const headers = {}
	headers['Authorization'] = 'Bearer ' + JsUtil.getAppItem("token");
	headers['weToken'] = JsUtil.getAppItem('weToken');
	headers['mi'] = JsUtil.getAppItem('currUserId');
	headers['oi'] = JsUtil.getAppItem('currOrgId');
	headers['ot'] = JsUtil.getAppItem('currOrgType');

	if ('multipart/form-data' != contentType)
		headers['content-type'] = contentType;
	uri = JsUtil.getUri(uri);

	const params = {
		url: uri,
		method: 'post',
		header: headers,
		data: param
	}

	Taro.request(params).then((response) => {
		console.log(uri + ' asyncHttpPost response: ', response);
		let result = response.data;
		if (response.statusCode < 200 || response.statusCode > 300) {
			if (response.data) result = response.data.error;
			else result = response.statusCode;

			throw Error(result);
		}

		return result
	}).then(succ).catch(err)
}
JsUtil.asyncHttpPost = asyncHttpPost

