const uploadSingleFile = (that, file, succ, err) => {
	if (file.duration && file.size > 512 * 1024 * 1024) {
		JsUtil.showMsgBox(that, '上传文件不能大于500M');
		return;
	} else if (!file.duration && file.size > 1024 * 1024) {
		JsUtil.showMsgBox(that, '上传文件不能大于1M');
		return;
	}

	const headers = {}
	//headers['content-type'] = 'multipart/form-data'
	headers['Authorization'] = 'Bearer ' + JsUtil.getAppItem("token");
	//headers['fileCategory'] = file.fileCategory || cnst.BIZZ_CAT_COMMON;
    const  fileCategory = file.fileCategory || cnst.BIZZ_CAT_COMMON;
	const filePath = file.path || file.tempFilePath;
	const targetUri = file.targetUri || '/rs/pub/file/upload/create';
	console.log('uploadSingleFile targetUri: ', targetUri);
	const params = {
		url: JsUtil.getUri(targetUri)+'?fileCategory='+fileCategory,
		name: 'uploadFile',
		header: headers,
		filePath: filePath,
		success: succ,
		fail: err
	}

	console.log('uploadSingleFile params: ', params);
	const uploadTask = Taro.uploadFile(params);

	uploadTask.onProgressUpdate((res) => {
		console.log('上传进度', res.progress)
		console.log('已经上传的数据长度', res.totalBytesSent)
		console.log('预期需要上传的数据总长度', res.totalBytesExpectedToSend)
	});

	return uploadTask;
}
JsUtil.uploadSingleFile = uploadSingleFile;

const onFileUpload = (that, args, onUploadSucc, onUploadFail, onOpenProgress) => {
	const count = args.count || 1;
	const fileType = args.fileType || 'i';
	const category = args.category;
	const taskCallback = args.taskCallback;

	const onSucc = result => {
		if (onOpenProgress) onOpenProgress()
		const tempFiles = result.tempFiles || [result];
		const msg = '网络错误或文件大于100M';

		tempFiles.forEach((item, idx) => {
			const succ = (result) => {
				if (result.statusCode == 200) {
					result = JSON.parse(result.data);
					if (JsUtil.handleMessage(that, result)) {
						onUploadSucc(result, idx);
					} else {
						console.log('onUploadSucc fail 0: ', result);
						if (onUploadFail) onUploadFail(msg);
						else JsUtil.showMsgBox(that, msg);
					}
				} else {
					console.log('onUploadSucc fail 2: ', result);
					if (onUploadFail) onUploadFail(msg);
					else JsUtil.showMsgBox(that, msg);
				}
			}

			const err = (result) => {
				console.log('onUploadFail err: ', result);
				if (onUploadFail) onUploadFail();

				if (result.statusCode)
					result = JSON.parse(result.data);

				JsUtil.handleMessage(that, result, null, false);
			}

			item.fileCategory = category;
			const uploadTask = JsUtil.uploadSingleFile(that, item, succ, err);
			if (taskCallback) {
				taskCallback(uploadTask);
			}
		});
	}

	if (fileType == 'v') {
		Taro.chooseVideo({
			count: count,
			success: (result) => onSucc(result),
			fail: (result) => {
				console.log('onUploadFail fail on chooseVideo');
			},
		});
	} else {
		Taro.chooseImage({
			count: count,
			success: (result) => onSucc(result),
			fail: (result) => {
				console.log('onUploadFail fail on chooseImage');
			},
		});
	}
}
JsUtil.onFileUpload = onFileUpload;

const handleMessage = (that, result, isPopUponSucc, isNotPopUponFail) => {
	let isContinue = false;
	let message = '';

	if (result.code == cnst.CODE_COMM_0_SUCCESS) {
		isContinue = true;
		message = result.message;
	} else if (result.code == cnst.CODE_COMM_GRANT_EXPIRED || result == 401) {
		message = result.message || '请先登录';
		JsUtil.go2Login(that, result.message);
	} else if (result.code == cnst.CODE_COMM_INPUT_ERROR) {
		if (result.body) {
			if (Array.isArray(result.body)) {
				message = result.message + ':\n' + JsUtil.collectMessage(result.body, that.labels0);
			} else {
				message = result.message + ':\n' + result.body;
			}
		} else {
			message = result.message;
		}
	} else if (result.code) {
		message = result.message;
	} else if (result.status) {
		message = '网络异常:\n' + result.status;
	} else if (result.errMsg) {
		message = '服务超时:\n' + result.errMsg;
	} else {
		console.log('result: ', result);
		message = '操作失败:\n' + result;
	}

	if ((isContinue && isPopUponSucc) || (!isContinue && !isNotPopUponFail)) {
		showMsgBox(that, message)
	} else {
		console.log('Util handleMessage: ' + message);
	}

	return isContinue;
}
JsUtil.handleMessage = handleMessage

