const collectMessage = (msgArr, labels) => {
	let msg = ''
	for (let i = 0; i < msgArr.length; i++) {
		msg = `${msg}${labels[msgArr[i]] || msgArr[i]}:${msgArr[++i]}\n`
	}

	return msg
}
JsUtil.collectMessage = collectMessage

const validateFields = name => {
	let validJs = JsUtil.getAppItem('fields_' + name);
	//console.table('fields_' + name + ' validJs: ' + validJs)
	if (!validJs) return null;

	return function(that, validator, fieldName, fieldValue) {
		const validField = validJs[fieldName];
		if (!validField) return;

		let validMsg = validField['NotEmpty'];
		if (validMsg) {
			if (validator.isEmpty(fieldValue)) {
				that.labels[fieldName] = validMsg;
				that.state.errors[fieldName] = true;

				return;
			} else {
				that.labels[fieldName] = that.labels0[fieldName];
				that.state.errors[fieldName] = false;
			}
		}

		validMsg = validField['NotBlank'];
		if (validMsg) {
			if (validator.isEmpty(fieldValue)) {
				that.labels[fieldName] = validMsg;
				that.state.errors[fieldName] = true;

				return;
			} else {
				that.labels[fieldName] = that.labels0[fieldName];
				that.state.errors[fieldName] = false;
			}
		}

		validMsg = validField['NotNull'];
		if (validMsg) {
			if (validator.isEmpty(fieldValue)) {
				that.labels[fieldName] = validMsg;
				that.state.errors[fieldName] = true;

				return;
			} else {
				that.labels[fieldName] = that.labels0[fieldName];
				that.state.errors[fieldName] = false;
			}
		}

		validMsg = validField['Min'];
		if (validMsg) {
			if (validator.isFloat(fieldValue, { min: parseInt(validField['Min1']) })) {
				that.labels[fieldName] = that.labels0[fieldName];
				that.state.errors[fieldName] = false;
			} else {
				that.labels[fieldName] = validMsg;
				that.state.errors[fieldName] = true;

				return
			}
		}

		validMsg = validField['Max'];
		if (validMsg) {
			if (validator.isFloat(fieldValue, { max: parseInt(validField['Max1']) })) {
				that.labels[fieldName] = that.labels0[fieldName];
				that.state.errors[fieldName] = false;
			} else {
				that.labels[fieldName] = validMsg;
				that.state.errors[fieldName] = true;

				return
			}
		}

		validMsg = validField['Digits'];
		if (validMsg) {
			if (fieldValue.lastIndexOf('.') > 0 && fieldValue.substring(fieldValue.lastIndexOf('.') + 1).length > parseInt(validField['Digits2'])) {
				that.labels[fieldName] = that.labels0[fieldName];
				that.state.errors[fieldName] = false;
			} else {
				that.labels[fieldName] = validMsg;
				that.state.errors[fieldName] = true;

				return
			}
		}

		validMsg = validField['Size'];
		if (validMsg) {
			if (validator.isLength(fieldValue, { min: parseInt(validField['Size1']), max: parseInt(validField['Size2']) })) {
				that.labels[fieldName] = that.labels0[fieldName];
				that.state.errors[fieldName] = false;
			} else {
				that.labels[fieldName] = validMsg;
				that.state.errors[fieldName] = true;

				return;
			}
		}

		validMsg = validField['Rang'];
		if (validMsg) {
			if (validator.isRange(fieldValue, { min: parseInt(validField['Rang1']), max: parseInt(validField['Rang2']) })) {
				that.labels[fieldName] = that.labels0[fieldName];
				that.state.errors[fieldName] = false;
			} else {
				that.labels[fieldName] = validMsg;
				that.state.errors[fieldName] = true;

				return
			}
		}

		validMsg = validField['Email'];
		if (validMsg) {
			if (validator.isEmail(fieldValue)) {
				that.labels[fieldName] = that.labels0[fieldName];
				that.state.errors[fieldName] = false;
			} else {
				that.labels[fieldName] = validMsg;
				that.state.errors[fieldName] = true;

				return
			}
		}

		validMsg = validField['URL'];
		if (validMsg) {
			if (validator.isURL(fieldValue)) {
				that.labels[fieldName] = that.labels0[fieldName];
				that.state.errors[fieldName] = false;
			} else {
				that.labels[fieldName] = validMsg;
				that.state.errors[fieldName] = true;

				return
			}
		}
	}
}
JsUtil.validateFields = validateFields;

const validateForm = name => {
	let validJs = JsUtil.getAppItem('fields_' + name);
	// console.log(validJs)
	if (!validJs) return null;

	return function(validator, labels, params) {
		let result = '';
		let validMsg = '';
		let validField;
		let fieldLabel;
		let fieldValue;
		Object.keys(validJs).map(fieldName => {
			if (fieldName == 'memberId' || fieldName == 'orgId') return true;
			validField = validJs[fieldName];
			fieldLabel = labels[fieldName]
			if (!fieldLabel) return true;
			fieldValue = params[fieldName] + '';
			validMsg = validField['NotEmpty'] || validField['NotBlank'] || validField['NotNull'];
			if (validMsg) {
				if ('undefined' == fieldValue || validator.isEmpty(fieldValue)) {
					result = `${result}${fieldLabel}: ${validMsg}\n`
				}
			}

			validMsg = validField['Min'];
			if (validMsg) {
				if (!validator.isFloat(fieldValue, { min: validField['Min1'] })) {
					result = `${result}${fieldLabel}: ${validMsg}\n`
				}
			}

			validMsg = validField['Max'];
			if (validMsg) {
				if (!validator.isFloat(fieldValue, { max: validField['Max1'] })) {
					result = `${result}${fieldLabel}: ${validMsg}\n`
				}
			}

			validMsg = validField['Digits'];
			if (validMsg) {
				if (fieldValue.lastIndexOf('.') > 0 && fieldValue.substring(fieldValue.lastIndexOf('.') + 1).length > validField['Digits2']) {
					result = `${result}${fieldLabel}: ${validMsg}\n`
				}
			}

			validMsg = validField['Size'];
			if (validMsg) {
				if (!validator.isLength(fieldValue, { min: validField['Size1'], max: validField['Size2'] })) {
					result = `${result}${fieldLabel}: ${validMsg}\n`
				}
			}

			validMsg = validField['Range'];
			if (validMsg) {
				if (!validator.isLength(fieldValue, { min: validField['Range1'], max: validField['Range2'] })) {
					result = `${result}${fieldLabel}: ${validMsg}\n`
				}
			}

			validMsg = validField['Email'];
			if (validMsg) {
				if (!validator.isEmpty(fieldValue)) {
					result = `${result}${fieldLabel}: ${validMsg}\n`
				}
			}

			validMsg = validField['URL'];
			if (validMsg) {
				if (!validator.isUrl(fieldValue)) {
					result = `${result}${fieldLabel}: ${validMsg}\n`
				}
			}
		})

		return result;
	}
}
JsUtil.validateForm = validateForm;

const init = (that, uri, name) => {
	const getValidateFunctions = () => ({
		validateFields: validateFields(name),
		validateForm: validateForm(name)
	});

	const initPromise = new Promise(resolve => {
		JsUtil.asyncHttpGet(that,
			uri + '?apiType=3',
			result => {
				if (JsUtil.handleMessage(that, result)) {
					let validJs = result.body.fieldJs;
					JsUtil.setAppItem('fields_' + name, validJs);
					validJs = result.body.formJs;
					JsUtil.setAppItem('form_' + name, validJs);
					resolve(getValidateFunctions());
				}
			},
			error => JsUtil.handleMessage(that, error)
		);
	});

	initPromise.catch(() => {
		console.log("initPromise Rejected");
	});

	return initPromise;
}
JsUtil.init = init;

