const setAppItem = (key, value) => {
	let val, storedVal
	switch (typeof value) {
		case 'number':
			val = JSON.stringify(value)
			storedVal = JSON.stringify({
				type: 'number',
				payload: val
			})
			break
		case 'object':
			val = JSON.stringify(value)
			storedVal = JSON.stringify({
				type: 'object',
				payload: val
			})
			break
		case 'string':
			storedVal = JSON.stringify({
				type: 'string',
				payload: value
			})
			break
		default:
			storedVal = JSON.stringify({
				type: 'unknown',
				payload: value
			})
	}

	if (process.env.TARO_ENV === 'rn') {
		if (!storedVal) return;
		storage.set(key, storedVal);
	} else {
		Taro.setStorageSync(key, storedVal)
	}
}
JsUtil.setAppItem = setAppItem

const getAppItem = key => {
	let returnVal
	try {
		let value = undefined;
		if (process.env.TARO_ENV === 'rn') {
			value = storage.getString(key);
		} else {
			value = Taro.getStorageSync(key)
		}

		if (!value) {
			return value
		}
		const valObj = JSON.parse(value)
		switch (valObj.type) {
			case 'number':
				returnVal = Number(valObj.payload)
				break
			case 'string':
				returnVal = valObj.payload
				break
			case 'object':
				returnVal = JSON.parse(valObj.payload)
				break
			default:
				returnVal = valObj.payload
		}
		return returnVal
	} catch (error) {
		return null
	}
}
JsUtil.getAppItem = getAppItem

