const getInnerElement = (arr, srcField, srcValue) => {
	const obj = arr.find(item => item[srcField] == srcValue);
	if (!obj) return {};

	return obj;
}
JsUtil.getInnerElement = getInnerElement

const getInnerValue = (arr, srcField, srcValue, targetField) => {
	const obj = arr.find(item => item[srcField] == srcValue);
	if (!obj) return undefined;

	return obj[targetField];
}
JsUtil.getInnerValue = getInnerValue

const mobileFormat = (that, phoneNo) => {
	if (!phoneNo) {
		JsUtil.showMsgBox(that, '请输入手机号')
		return false;
	} else if (!/^1[3|4|5|6|7|8|9][0-9]\d{8}$/.test(phoneNo)) {
		JsUtil.showMsgBox(that, '手机号码不规范，请重新输入');
		return false;
	}

	return true;
}
JsUtil.mobileFormat = mobileFormat;

const setProperties = (obj, value) => {
	return Object.assign({}, ...Object.keys(obj).map(key => ({ [key]: value })));
}
JsUtil.setProperties = setProperties;

const appendTime = (dateStr) => {
	if (dateStr == ' ') return '';

	if (dateStr)
		return dateStr + ' 00:00:00';

	return dateStr;
}
JsUtil.appendTime = appendTime;

const setOperatorOptions = (that, opOptionType, checkedId) => {
	if (!opOptionType) return;

	if (opOptionType == cnst.OP_OPTION_TYPE_ALL) {
		const succ = (result) => {
			if (JsUtil.handleMessage(that, result)) {
				const operatorOptions = [{ first: cnst.ID_HQ, second: cnst.NAME_HQ, third: '愉快花园3栋608' }, ...result.body];
				let operatorId = undefined;
				let operatorName = '';
				if (operatorOptions.length > 0) {
					if (checkedId === '') {
						operatorId = operatorOptions[0].first;
						operatorName = operatorOptions[0].second;
						that.setState({ operatorId: operatorId, operatorName });
					} else if (checkedId) {
						operatorId = checkedId;
						operatorName = operatorOptions.find(item => item.first == checkedId).second;
						that.setState({ operatorId: operatorId, operatorName });
					}
				}

				that.setState({ operatorOptions: operatorOptions });
			}
		}

		const err = (result) => {
			JsUtil.handleMessage(that, result)
		}

		JsUtil.asyncHttpPost(that, '/op/pub/name/search', JSON.stringify({}), succ, err);
	} else if (opOptionType == cnst.OP_OPTION_TYPE_SUPPLIER) {
		const succ = (result) => {
			if (JsUtil.handleMessage(that, result)) {
				const operatorOptions = result.body;
				let operatorId = undefined;
				let operatorName = '';
				if (operatorOptions.length > 0) {
					if (checkedId === '') {
						operatorId = operatorOptions[0].first;
						operatorName = operatorOptions[0].second;
						that.setState({ operatorId: operatorId, operatorName });
					} else if (checkedId) {
						operatorId = checkedId;
						operatorName = operatorOptions.find(item => item.first == checkedId).second;
						that.setState({ operatorId: operatorId, operatorName });
					}
				}

				that.setState({ operatorOptions: operatorOptions });
			}
		}

		const err = (result) => {
			JsUtil.handleMessage(that, result)
		}

		const supplierId = JsUtil.getAppItem('currOrgId');
		JsUtil.asyncHttpPost(that, '/sp/pub/op/name/search', "supplierId=" + supplierId, succ, err, "application/x-www-form-urlencoded");
	} else if (opOptionType == cnst.OP_OPTION_TYPE_SERVICE_STATION) {
		const succ = (result) => {
			if (JsUtil.handleMessage(that, result)) {
				const operatorOptions = result.body;
				let operatorId = undefined;
				let operatorName = '';
				if (operatorOptions.length > 0) {
					if (checkedId === '') {
						operatorId = operatorOptions[0].first;
						operatorName = operatorOptions[0].second;
						that.setState({ operatorId: operatorId, operatorName });
					} else if (checkedId) {
						operatorId = checkedId;
						operatorName = operatorOptions.find(item => item.first == checkedId).second;
						that.setState({ operatorId: operatorId, operatorName });
					}
				}

				that.setState({ operatorOptions: operatorOptions });
			}
		}

		const err = (result) => {
			JsUtil.handleMessage(that, result)
		}

		const serviceStationId = JsUtil.getAppItem('currOrgId');
		JsUtil.asyncHttpPost(that, '/ss/pub/op/name/search', "serviceStationId=" + serviceStationId, succ, err, "application/x-www-form-urlencoded");
	} else if (opOptionType == cnst.OP_OPTION_TYPE_SELF) {
		const operatorId = JsUtil.getAppItem('currOrgId');
		const currOrgName = JsUtil.getAppItem('currOrgName');
		const operatorOptions = [{ first: operatorId, second: currOrgName, third: '' }];

		that.setState({ operatorOptions: operatorOptions, operatorId: operatorId, operatorName: currOrgName });
	} else {
		const operatorId = cnst.ID_HQ;
		const currOrgName = cnst.NAME_HQ;
		const operatorOptions = [{ first: operatorId, second: currOrgName, third: '' }];

		that.setState({ operatorOptions: operatorOptions, operatorId: operatorId, operatorName: currOrgName });
	}
}
JsUtil.setOperatorOptions = setOperatorOptions;

const uploadFileChanged = (that, fieldName) => (value, opType, idx) => {
	const fileName = fieldName + 'File';
	if ('add' == opType) {
		const succ = (result) => {
			if (result.statusCode) result = JSON.parse(result.data);
			if (JsUtil.handleMessage(that, result)) {
				that.setState({ [fieldName]: result.body });
			}
		}

		const err = (result) => {
			if (result.statusCode) result = JSON.parse(result.data);
			JsUtil.handleMessage(that, result)
		}

		JsUtil.uploadSingleFile(that, value[0].file, succ, err);
		const newValue = [];
		value[1] ? newValue.push(value[1]) : newValue.push(value[0]);
		that.setState({ [fileName]: newValue });
	} else if ('remove' == opType) {
		that.setState({ [fileName]: [] });
	}
}
JsUtil.uploadFileChanged = uploadFileChanged;

const onValueChange = (that, name) => (item) => {
	let value = item.value;
	if (!value) value = item.id;

	that.setState({ [name]: value })
}
JsUtil.onValueChange = onValueChange;

const onIdChange = (that, name) => (item) => {
	const value = item.id;

	that.setState({ [name]: value })
}
JsUtil.onIdChange = onIdChange;

const delEleByIdx = (arr, idx) => {
	if (arr.length == 1) return [];
	arr.splice(idx, 1);
	return arr;
}
JsUtil.delEleByIdx = delEleByIdx;

const delEleByValue = (arr, val) => {
	const set = new Set(arr);
	set.delete(val);
	return Array.from(set);
}
JsUtil.delEleByValue = delEleByValue;

const containEleValue = (arr2, val) => {
	for (let i = 0; i < arr2.length; i++) {
		const ele = arr2[i]
		if (Array.isArray(ele)) {
			const result = ele.find(e => e == val);
			if (result) return true;
		}
	}

	return false;
}
JsUtil.containEleValue = containEleValue;

const getCurrPosition = (that) => {
	if (process.env.TARO_ENV != 'h5') return;

	const geolocation = new qq.maps.Geolocation("HT7BZ-XLQ3I-QOOGP-UEPJU-5M2BE-55FZU", "pcgmall");
	geolocation.watchPosition(position => {
		const longitude = position.lng;
		const latitude = position.lat;

		JsUtil.setAppItem('longitude', longitude);
		JsUtil.setAppItem('latitude', latitude);

		if (that)
			that.props.onRegionchange(longitude, latitude);
	});
}
JsUtil.getCurrPosition = getCurrPosition;

