const sleep = (delay) => {
	const sleepPromise = new Promise((resolve) => setTimeout(resolve, delay));
	sleepPromise.catch(() => {
		console.log("sleepPromise Rejected");
	});
	return sleepPromise;
}
JsUtil.sleep = sleep;

const createWechat = (that) => {
	wx.login({
		success(res) {
			if (res.code) {
				const weId = JsUtil.getAppItem('weId');
				if (weId) {
					//return;
				}

				const succ = (result) => {
					if (JsUtil.handleMessage(that, result)) {
						const item = result.body;
						console.log('wx.login succ weId=' + item.weId);
						JsUtil.setAppItem('weId', item.weId);
						JsUtil.setAppItem('weToken', item.weToken);

						const recommend = JsUtil.getAppItem('recommend');
						if (recommend) {
							JsUtil.setAppItem('recommend', null);
							JsUtil.createRecommend(that, recommend);
						}
					}
				}

				const err = (result) => {
					JsUtil.handleMessage(that, result)
				}
				console.log('wx.login weId=' + weId);
				console.log('wx.login res.code=' + res.code);

				JsUtil.asyncHttpPost(that, '/mb/pub/wechat/2/create', 'wxCode=' + res.code, succ, err, cnst.REQUEST_TYPE_WWW);
			}
		}
	});
}
JsUtil.createWechat = createWechat;

const createRecommend = (that, recommend) => {
	let mallType = recommend.mallType;
	if (!mallType)
		mallType = JsUtil.getAppItem('currMallType');
	const commodityId = recommend.commodityId;
	const wePid = recommend.wePid;
	const weId = JsUtil.getAppItem('weId')
	const memberPid = recommend.memberPid;
	const memberId = that.currUserId;
	const lastProvinceId = recommend.lastProvinceId;
	const operatorId = recommend.operatorId;

	if ((wePid && weId && wePid != weId) || memberPid && memberId) {
		console.log('wePid is not null')
		const param = {
			memberPid: memberPid,
			memberId: memberId,
			commodityId: commodityId,
			wePid: wePid,
			weId: weId,
		}

		const succ = (result) => {
			if (JsUtil.handleMessage(that, result)) {
				console.log('recommend created, result is ', result)
			}
		}

		const err = (result) => {
			if (JsUtil.handleMessage(that, result)) {
			}
		}

		JsUtil.asyncHttpPost(that, '/mb/pub/recommend/2/create', JSON.stringify(param), succ, err);
	}

	if (operatorId) {
		console.log('operatorId is not null')
		JsUtil.setAppItem('lastProvinceId', lastProvinceId)
		JsUtil.setAppItem('currOperatorId', operatorId)
		JsUtil.setAppItem('lastOperatorId', operatorId)
		JsUtil.setAppItem('currMallType', mallType)
	}
}
JsUtil.createRecommend = createRecommend;

const getFromAddrIds = (deliveryType, fromAddrIds0) => {
	//fromAddrIds0为deliveryType分隔的地址ids
	const fromAddrIds = [];
	let id = null;
	let isCurrType = false;
	for (let i = 0; i < fromAddrIds0.length; i++) {
		id = fromAddrIds0[i];
		if (isCurrType) {
			if (id < 20)
				return fromAddrIds;

			if (i == fromAddrIds0.length - 1) {
				fromAddrIds.push(id);

				return fromAddrIds;
			}

			fromAddrIds.push(id);
		}

		if (deliveryType == id) {
			isCurrType = true;
		}
	}

	return fromAddrIds;
}
JsUtil.getFromAddrIds = getFromAddrIds;


export default JsUtil
