import React from 'react'
import JsUtil from 'tsx/common/JsUtil'
import cnst from 'tsx/common/Constant'
import ColumnLayout from 'tsx/layout/ColumnLayout';
import RowLayout from 'tsx/layout/RowLayout';
import CellView from 'tsx/view/CellView';
import GeneralButton from 'tsx/form/GeneralButton'
import LegenImage from 'tsx/view/LegenImage'
import LinkTag from 'tsx/view/LinkTag'

class ImagePicker extends React.Component {
  that = this;
  
  onAddImage = (event) =>{
      JsUtil.onFileUpload(this.props.that, {count: this.props.count, category: this.props.category}, this.uploadCallback);
  }
  
  uploadCallback = (result) => {
	  console.log('uploadCallback result: ', result)
      const files = this.props.count==1? []: this.props.files;
      files.push({url: JsUtil.IMAGE_PREFIX + result.body})
      const that = this.that;
	  console.log('uploadCallback that: ', that)
      that.props.onChange(files, 'add', result.body);
  }
  
  onRemoveImage = (idx, event) => {
      let files = this.props.files;
      files = JsUtil.delEleByIdx(files, idx);
      if(this.props.that)
      this.props.that.onChange(files, 'rm', idx);
      else
      this.props.onChange(files, 'rm', idx);
  }
  
  getShownContent = (files) => {
      const disabled = this.props.disabled;
      const width = disabled? cnst.NUM_WIDTH_MAIN: 80;
      const height = disabled? cnst.NUM_WIDTH_MAIN/2: 80;
      const totalSize = files.length;
      const columnSize = disabled? 1: this.props.columnSize||4;
      const rowArr = []
      let itemArr = []
      
      files.map((item, idx)=>{
          itemArr.push( <CellView key={'img-'+idx}><LegenImage src={item.url} width={width} height={height}  />
          {disabled!=true &&
          <LinkTag plain={true} type='primary' onClick={event=>this.onRemoveImage(idx, event)} position={'absolute'} zIndex={9} justifyContent='flex-start' alignItems='flex-start' alignSelf='flex-start' width={10} height={10} backgroundColor={[255,255,255,1]}>-</LinkTag>
          }
          </CellView>)
          if(idx==totalSize-1 && totalSize%columnSize!=0 && disabled!=true){
             itemArr.push( <CellView key={'add-'+idx}><GeneralButton onClick={this.onAddImage} alignSelf='flex-end' width={80} height={80}>+</GeneralButton></CellView>)
          }
          
          if( idx%columnSize==columnSize-1||idx==totalSize-1){
             rowArr.push(<RowLayout key={'row-'+idx} >{itemArr}</RowLayout>)
             itemArr = []
          }
      })
      
      if((totalSize==0 || totalSize%columnSize==0) && disabled!=true){
          rowArr.push(<RowLayout  key={'row-'+(totalSize+1)} ><CellView><GeneralButton onClick={this.onAddImage}  alignSelf='flex-end' width={80} height={80}>+</GeneralButton></CellView></RowLayout>)
      }
      
      return rowArr
  }
  
  render() {
    const showContent = this.getShownContent(this.props.files);
      
    return <ColumnLayout>
      {showContent}
    </ColumnLayout>
  }
}

export default ImagePicker
