import React from 'react'
import JsUtil from 'tsx/common/JsUtil'
import RowLayout from 'tsx/layout/RowLayout'
import ColumnLayout from 'tsx/layout/ColumnLayout'
import SimpleIcon from 'tsx/view/SimpleIcon'
import TextLabel from 'tsx/view/TextLabel'

class CardLayout extends React.Component {
      
  render() {
    const alignSelf =  JsUtil.getCssStrValue(this.props.alignSelf, 'center')
      
    return (
      <ColumnLayout width={this.props.width} {...this.props}>
      <RowLayout justifyContent='flex-start' width={this.props.width}>
        <SimpleIcon value={this.props.thumb}></SimpleIcon>
        <TextLabel>{this.props.title}</TextLabel>
      </RowLayout>
        {this.props.children}
      </ColumnLayout>
    )
  }
}

export default CardLayout
