import React from 'react'
import { Text } from '@tarojs/components'
import { styled } from '@linaria/react'
import JsUtil from 'tsx/common/JsUtil'

const TextLabel0 = styled(Text)`
  display: flex;
  flex-direction: row;
  justify-content: ${props => props.justifyContent};
  align-items: ${props => props.alignItems};
  flex-wrap: ${props => props.flexWrap};
  width: ${props => props.width};
  height: ${props => props.height};
`

class TextLabel extends React.Component {

	render() {
		const justifyContent = JsUtil.getCssStrValue(this.props.justifyContent, 'flex-start')
		const alignItems = JsUtil.getCssStrValue(this.props.alignItems, 'flex-start')
		const flexWrap = JsUtil.getCssStrValue(this.props.flexWrap, 'nowrap')

		const width = JsUtil.getCssWidthValue(this.props.width, 'auto');
		const height = JsUtil.getCssHeightValue(this.props.height, 'auto');

		const color = JsUtil.getColorValue(this.props.color, 'black');
		const fontSize = JsUtil.getCssNumValue(this.props.fontSize, 14);

		const backgroundColor = JsUtil.getColorValue(this.props.backgroundColor, [255, 0, 255, 0]);

		return (
			<TextLabel0 justifyContent={justifyContent} alignItems={alignItems}
				flexWrap={flexWrap} width={width} height={height}
				onClick={this.props.onClick}
				style={{ color: color, fontSize: fontSize }}
			>
				{this.props.children}
			</TextLabel0>
		)
	}
}

export default TextLabel
