import React from 'react'
import CharacteristicLayout from 'tsx/layout/CharacteristicLayout'
import Level2View from 'tsx/view/Level2View'

class CarouselLayout extends React.Component {

  render() {
    return (
      <CharacteristicLayout messages={this.props.messages}>
          {this.props.children}
      </CharacteristicLayout>
    )
  }
}

export default CarouselLayout
