import React from 'react'
import Taro from '@tarojs/taro'
import { styled } from '@linaria/react'
import { View } from '@tarojs/components'
import JsUtil from 'tsx/common/JsUtil'
import cnst from 'tsx/common/Constant'
import TopNavBar from 'tsx/component/TopNavBar'
import TaroModal from 'tsx/component/TaroModal'

const View0 = styled(View)`
display: flex;
flex-direction: column;
justify-content: center;
align-items: center;
`

class FootLayout extends React.Component {
	state = {
		marginTop: 46
	}

	componentDidMount() {
		Taro.getSystemInfo({})
			.then(res => {
				const statusBarHeight = res.statusBarHeight || 20
				let topBarHeight = 36;
				let marginTop = 30;
				if (process.env.TARO_ENV === 'rn') {
					marginTop = 1
				} else if (process.env.TARO_ENV === cnst.TARO_ENV_WEAPP) {
					topBarHeight += statusBarHeight
					marginTop = topBarHeight + 1
				} else if (process.env.TARO_ENV === 'h5') {
					topBarHeight -= 6
					marginTop = topBarHeight + 1
				}

				this.setState({ topBarHeight: topBarHeight, marginTop: marginTop })
			})
	}

	render() {
		const topBarHeight = this.state.topBarHeight;
		const marginTop = this.state.marginTop;
		const backgroundColor = JsUtil.getColorValue(cnst.COLOR_BACKGROUND_NEAR_WHITE);

		return (
			<View style={{ flex: 1, marginTop: marginTop, backgroundColor: backgroundColor }}>
				<TopNavBar height={topBarHeight} topNavBarTitle={this.props.topNavBarTitle} />
				<TaroModal that={this.props.that} zIndex={8888}></TaroModal>
				{this.props.children}
			</View>
		)
	}
}

export default FootLayout
