import React from 'react'
import CarouselLayout from 'tsx/layout/CarouselLayout'
import Level2View from 'tsx/view/Level2View'

class CategoryLayout extends React.Component {

  render() {
    return (
      <CarouselLayout messages={this.props.messages}>
          {this.props.children}
      </CarouselLayout>
    )
  }
}

export default CategoryLayout
