import React from 'react'
import cnst from 'tsx/common/Constant'
import RowLayout from 'tsx/layout/RowLayout'
import ColumnLayout from 'tsx/layout/ColumnLayout'

class GridLayout extends React.Component {
    columnSize = this.props.columnSize
    
    getChildren = (childrenWithPropsArray) =>{
      const totalSize = childrenWithPropsArray.length
      const rowArr = []
      let itemArr = []
      
      childrenWithPropsArray.map((item, idx)=>{
            itemArr.push( item)
          if(idx%this.columnSize==1||idx==totalSize-1){
             rowArr.push(<RowLayout key={idx} width={cnst.NUM_WIDTH_MAIN/this.columnSize} >{itemArr}</RowLayout>)
             itemArr = []
          }
      })
      
      return rowArr;
    }
    
  render() {
    const data = this.props.data
    if(!data) return null;
    const childrenWithPropsArray = []
    
    data.map((item, idx)=>{
    const childrenWithProps = React.Children.map( this.props.children, (child, idx) => {
      // Checking isValidElement is the safe way and avoids a TS error too.
      if (React.isValidElement(child)) {
        return React.cloneElement(child, { shownObj:  item})
      }

      return child;
    });
    
    childrenWithPropsArray.push(childrenWithProps)
    })
    
    const showElements = this.getChildren(childrenWithPropsArray);
    
    return (
      <ColumnLayout width={cnst.NUM_WIDTH_MAIN/2}>
            {showElements}
      </ColumnLayout>
    )
  }
}

export default GridLayout
