import React from 'react'
import cnst from 'tsx/common/Constant'
import FootLayout from 'tsx/layout/FootLayout'
import BrView from 'tsx/view/BrView'
import ColumnLayout from 'tsx/layout/ColumnLayout'
import ScrollableView from 'tsx/view/ScrollableView'
import Level1View from 'tsx/view/Level1View'
import BottomOpBar from 'tsx/component/BottomOpBar'
import TextLabel from 'tsx/view/TextLabel'

class HeadLayout extends React.Component {
	render() {
		return (
			<FootLayout that={this.props.that} topNavBarTitle={this.props.topNavBarTitle} >
				<ScrollableView>
					<ColumnLayout width={cnst.NUM_WIDTH_MAIN} backgroundColor={cnst.COLOR_BACKGROUND_WHITE} >
						<BrView height={cnst.NUM_HEIGHT_LINE} backgroundColor={cnst.COLOR_BACKGROUND_WHITE} />
						{this.props.children}

						<BrView height={36} backgroundColor={cnst.COLOR_BACKGROUND_NEAR_WHITE} />
						<Level1View alignItems='center'>
							<TextLabel justifyContent='center'>Copyright © 2012-2024 版权所有</TextLabel>
							<TextLabel justifyContent='center'>柏君商城 www.pc8g.com</TextLabel>
							<TextLabel justifyContent='center'>深圳市柏君科技有限公司</TextLabel>
							<TextLabel justifyContent='center'>粤ICP备18056790号</TextLabel>
							<TextLabel justifyContent='center'>粤公网安备 44030502002068号</TextLabel>
						</Level1View>
						<BrView height={66} backgroundColor={cnst.COLOR_BACKGROUND_NEAR_WHITE} />
					</ColumnLayout>
				</ScrollableView>

				{(this.props.bottomLabel0 || this.props.bottomLabel1 || this.props.bottomLabel2 || this.props.bottomLabel3 || this.props.bottomLabel4 || this.props.bottomLabel6 || this.props.bottomLabel8) &&
					<BottomOpBar bottomLabel0={this.props.bottomLabel0} bottomLabel1={this.props.bottomLabel1} bottomLabel2={this.props.bottomLabel2} bottomLabel3={this.props.bottomLabel3} bottomLabel4={this.props.bottomLabel4}
						bottomLabel5={this.props.bottomLabel5} bottomLabel6={this.props.bottomLabel6} bottomLabel7={this.props.bottomLabel7} bottomLabel8={this.props.bottomLabel8}
						bottomLabel9={this.props.bottomLabel9} bottomLabel10={this.props.bottomLabel10} shareLabel={this.props.shareLabel} that={this.props.that} imageUri={this.props.imageUri} postFunc={this.props.postFunc}
						bottomFunc0={this.props.bottomFunc0} bottomFunc1={this.props.bottomFunc1} bottomFunc2={this.props.bottomFunc2} bottomFunc3={this.props.bottomFunc3} bottomFunc4={this.props.bottomFunc4}
						bottomFunc5={this.props.bottomFunc5} bottomFunc6={this.props.bottomFunc6} bottomFunc7={this.props.bottomFunc7}
						bottomFunc8={this.props.bottomFunc8} bottomFunc9={this.props.bottomFunc9} bottomFunc10={this.props.bottomFunc10}
						shareFunc={this.props.shareFunc} bottom={this.props.bottom} />
				}
			</FootLayout >
		)
	}
}

export default HeadLayout
